html {
  position: relative;
  min-height: 100%;
}

body {
  overflow-x: hidden;
}

@supports (-webkit-overflow-scrolling: touch) {
  body {
    cursor: pointer;
  }
}

.logo {
  display: block;
  line-height: 70px;
  width: 260px;
  position: fixed;
  top: 0;
}

.logo span.logo-lg {
  display: block;
}

.logo span.logo-sm {
  display: none;
}

.logo.logo-light {
  display: block;
}

.logo.logo-dark {
  display: none;
}

.wrapper {
  height: 100%;
  overflow: hidden;
  width: 100%;
}

.content-page {
  margin-left: 260px;
  overflow: hidden;
  padding: 70px 12px 65px;
  min-height: 100vh;
}

.leftside-menu {
  width: 260px;
  z-index: 10;
  background: var(--ct-bg-leftbar-gradient);
  bottom: 0;
  position: fixed;
  top: 0;
  padding-top: 70px;
  box-shadow: var(--ct-box-shadow);
}

.side-nav {
  padding-left: 0;
  list-style-type: none;
}

.side-nav ul {
  list-style-type: none;
}

.side-nav .side-nav-link {
  color: var(--ct-menu-item);
  display: block;
  padding: 10px 30px;
  font-size: 0.9375rem;
  position: relative;
  transition: all 0.4s;
  list-style: none;
}

.side-nav .side-nav-link:hover, .side-nav .side-nav-link:focus, .side-nav .side-nav-link:active {
  color: var(--ct-menu-item-hover);
  text-decoration: none;
}

.side-nav .side-nav-link span {
  vertical-align: middle;
}

.side-nav .side-nav-link i {
  display: inline-block;
  line-height: 1.0625rem;
  margin: 0 10px 0 0;
  font-size: 1.1rem;
  vertical-align: middle;
  width: 20px;
}

.side-nav .menu-arrow {
  transition: transform .15s;
  position: absolute;
  right: 30px;
  display: inline-block;
  font-family: 'Material Design Icons';
  text-rendering: auto;
  line-height: 1.5rem;
  font-size: 1.1rem;
  transform: translate(0, 0);
}

.side-nav .menu-arrow:before {
  content: "\F0142";
}

.side-nav .badge {
  margin-top: 3px;
}

.side-nav .side-nav-item > a[aria-expanded="true"] > span.menu-arrow {
  transform: rotate(90deg);
}

.side-nav .side-nav-item.menuitem-active > a:not(.collapsed) > span.menu-arrow {
  transform: rotate(90deg);
}

.side-nav .side-nav-title {
  padding: 12px 30px;
  letter-spacing: .05em;
  pointer-events: none;
  cursor: default;
  font-size: 0.6875rem;
  text-transform: uppercase;
  color: var(--ct-menu-item);
  font-weight: 700;
}

.side-nav .menuitem-active > a {
  color: var(--ct-menu-item-active) !important;
}

*[dir="ltr"] .side-nav .side-nav-item .menu-arrow:before {
  content: "\F0141" !important;
}

.side-nav-second-level,
.side-nav-third-level,
.side-nav-forth-level {
  padding-left: 0;
}

.side-nav-second-level li a, .side-nav-second-level li .side-nav-link,
.side-nav-third-level li a,
.side-nav-third-level li .side-nav-link,
.side-nav-forth-level li a,
.side-nav-forth-level li .side-nav-link {
  padding: 8px 30px 8px 65px;
  color: var(--ct-menu-item);
  display: block;
  position: relative;
  transition: all 0.4s;
  font-size: 0.89rem;
}

.side-nav-second-level li a:focus, .side-nav-second-level li a:hover, .side-nav-second-level li .side-nav-link:focus, .side-nav-second-level li .side-nav-link:hover,
.side-nav-third-level li a:focus,
.side-nav-third-level li a:hover,
.side-nav-third-level li .side-nav-link:focus,
.side-nav-third-level li .side-nav-link:hover,
.side-nav-forth-level li a:focus,
.side-nav-forth-level li a:hover,
.side-nav-forth-level li .side-nav-link:focus,
.side-nav-forth-level li .side-nav-link:hover {
  color: var(--ct-menu-item-hover);
}

.side-nav-second-level li a .menu-arrow, .side-nav-second-level li .side-nav-link .menu-arrow,
.side-nav-third-level li a .menu-arrow,
.side-nav-third-level li .side-nav-link .menu-arrow,
.side-nav-forth-level li a .menu-arrow,
.side-nav-forth-level li .side-nav-link .menu-arrow {
  line-height: 1.3rem;
}

.side-nav-second-level li.active > a,
.side-nav-third-level li.active > a,
.side-nav-forth-level li.active > a {
  color: var(--ct-menu-item-active);
}

.side-nav-third-level li a, .side-nav-third-level li .side-nav-link {
  padding: 8px 30px 8px 80px;
}

.side-nav-forth-level li a, .side-nav-forth-level li .side-nav-link {
  padding: 8px 30px 8px 100px;
}

